"use client"

import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"

import { numberFormatter, dateFormatter } from "./data-table/utils/constants"
import { useOptionalLineDashboardContext } from "../context/LineDashboardProvider"

function SummaryTile({ label, value }) {
  return (
    <Card className="flex-1 min-w-[120px]">
      <CardHeader className="py-2">
        <CardTitle className="text-xs font-medium text-muted-foreground uppercase tracking-wide">
          {label}
        </CardTitle>
      </CardHeader>
      <CardContent className="py-2">
        <span className="text-lg font-semibold">{value}</span>
      </CardContent>
    </Card>
  )
}

export function LineSummaryPanel({ summary: providedSummary, isLoading, error }) {
  const context = useOptionalLineDashboardContext()
  const summary = providedSummary ?? context?.summary ?? null
  const effectiveLoading =
    typeof isLoading === "boolean" ? isLoading : context?.status === "loading"
  const effectiveError = error ?? context?.error ?? null

  if (effectiveLoading) {
    return (
      <Card>
        <CardContent className="py-6 text-sm text-muted-foreground">Loading summary‚Ä¶</CardContent>
      </Card>
    )
  }

  if (effectiveError) {
    return (
      <Card>
        <CardContent className="py-6 text-sm text-destructive">
          {effectiveError instanceof Error ? effectiveError.message : String(effectiveError)}
        </CardContent>
      </Card>
    )
  }

  if (!summary) {
    return (
      <Card>
        <CardContent className="py-6 text-sm text-muted-foreground">
          Summary data is not available yet.
        </CardContent>
      </Card>
    )
  }

  const formattedUpdatedAt = summary.latestUpdatedAt
    ? dateFormatter.format(new Date(summary.latestUpdatedAt))
    : null

  const tiles = [
    { label: "Total", value: numberFormatter.format(summary.totalCount ?? 0) },
    { label: "Active", value: numberFormatter.format(summary.activeCount ?? 0) },
    { label: "Completed", value: numberFormatter.format(summary.completedCount ?? 0) },
    { label: "Pending Jira", value: numberFormatter.format(summary.pendingJiraCount ?? 0) },
    { label: "Lots", value: numberFormatter.format(summary.lotCount ?? 0) },
  ]

  return (
    <div className="flex flex-col gap-3">
      <div className="flex flex-wrap gap-3">
        {tiles.map((tile) => (
          <SummaryTile key={tile.label} label={tile.label} value={tile.value} />
        ))}
      </div>
      {formattedUpdatedAt ? (
        <span className="text-xs text-muted-foreground">
          Latest update: {formattedUpdatedAt}
        </span>
      ) : null}
    </div>
  )
}
