"use client"

import { numberFormatter } from "../utils/constants"
import { formatCellValue } from "../utils/formatters"

function toHours(minutes) {
  return minutes / 60
}

export function DowntimeCell({ value }) {
  const minutes = Number(value)

  if (!Number.isFinite(minutes)) {
    return <span>{formatCellValue(value)}</span>
  }

  const hours = toHours(minutes)

  return (
    <div className="flex flex-col items-end leading-tight">
      <span className="text-sm font-semibold text-foreground">
        {numberFormatter.format(Math.round(minutes))} min
      </span>
      <span className="text-[10px] text-muted-foreground">{hours.toFixed(1)} hr</span>
    </div>
  )
}
