"use client"

import * as React from "react"

import { useLineDashboardData } from "../hooks/useLineDashboardData"

const LineDashboardContext = React.createContext(null)

export function LineDashboardProvider({
  lineId,
  children,
  loadData = false,
  initialData = null,
  fetcher,
}) {
  const { data, status, error, reload } = useLineDashboardData(lineId, {
    enabled: loadData,
    fetcher,
    initialData,
  })

  const resolvedData = loadData ? data : initialData

  const value = React.useMemo(
    () => ({
      lineId,
      status: loadData ? status : initialData ? "success" : status,
      error: loadData ? error : null,
      data: resolvedData,
      summary: resolvedData?.summary ?? null,
      trend: resolvedData?.trend ?? [],
      recent: resolvedData?.recent ?? [],
      reload,
    }),
    [error, initialData, lineId, loadData, reload, resolvedData, status]
  )

  return <LineDashboardContext.Provider value={value}>{children}</LineDashboardContext.Provider>
}

export function useOptionalLineDashboardContext() {
  return React.useContext(LineDashboardContext)
}

export function useLineDashboardContext() {
  const context = useOptionalLineDashboardContext()
  if (!context) {
    throw new Error("useLineDashboardContext must be used within a LineDashboardProvider")
  }
  return context
}
