"use client"

import * as React from "react"

const defaultFetcher = async (lineId) => {
  if (!lineId) {
    throw new Error("lineId is required to fetch dashboard data")
  }

  const params = new URLSearchParams({ lineId })
  const response = await fetch(`/api/line-dashboard?${params.toString()}`, {
    cache: "no-store",
  })

  if (!response.ok) {
    const payload = await response.json().catch(() => ({}))
    const message =
      typeof payload?.error === "string"
        ? payload.error
        : `Failed to load dashboard data (${response.status})`
    throw new Error(message)
  }

  return response.json()
}

export function useLineDashboardData(lineId, options = {}) {
  const { enabled = false, fetcher = defaultFetcher, initialData = null } = options

  const [state, setState] = React.useState(() => {
    if (initialData) {
      return { status: "success", data: initialData, error: null }
    }
    return { status: "idle", data: null, error: null }
  })

  const fetchRef = React.useRef(0)

  const reload = React.useCallback(async () => {
    if (!enabled) return null
    if (!lineId) {
      setState({ status: "idle", data: initialData, error: null })
      return null
    }

    const requestId = ++fetchRef.current
    setState((prev) => ({ ...prev, status: "loading", error: null }))

    try {
      const data = await fetcher(lineId)
      if (fetchRef.current !== requestId) return null
      setState({ status: "success", data, error: null })
      return data
    } catch (error) {
      if (fetchRef.current !== requestId) return null
      setState({ status: "error", data: null, error })
      return null
    }
  }, [enabled, fetcher, initialData, lineId])

  React.useEffect(() => {
    if (!enabled) return
    void reload()
  }, [enabled, reload])

  return { ...state, reload }
}
