"use client"

import { LineDashboardProvider } from "../context/LineDashboardProvider"
import { LineSummaryPanel } from "./LineSummaryPanel"
import { DataTable } from "./data-table"

export function LineDashboardPage({ lineId, loadSummary = false, initialData = null }) {
  return (
    <LineDashboardProvider lineId={lineId} loadData={loadSummary} initialData={initialData}>
      <div className="flex h-full min-h-0 flex-col gap-4">
        <LineSummaryPanel summary={initialData?.summary ?? null} />
        <div className="flex-1 min-h-0">
          <DataTable lineId={lineId} />
        </div>
      </div>
    </LineDashboardProvider>
  )
}
